.box-container {
  @apply flex flex-col p-2 rounded-lg pt-[0.75vw] w-[50vh] max-[1000px]:w-[300px] ;
  background-color: #fdfdfd;
  margin: auto 0 ;
}

.img-box {
  width: 100%;
  height: 40vh;
}

.img-box .img {
  @apply rounded-lg;
  width: 100%;
  height: 100%;
  object-fit: cover;
  margin: auto;
  transition: 500ms ease-in;
}

.ozellikler {
  @apply text-sm;
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 0.5rem;
  color: #3d3d43;
  font: bold;
}

.ozellik-kutusu {
  @apply flex w-fit mr-1 justify-center items-center rounded-lg p-1 mb-1;
  background-color: rgb(190, 190, 190);
}

.ozellik-icerik {
  @apply ml-1;
}

.fiyat-durum {
  display: flex;
  justify-content: space-between;
  margin: 0.4rem 0;
}

.fiyat {
  @apply text-lg font-bold;
}

.durum {
  @apply text-red-500;
  background-color: rgb(190, 190, 190);
  padding: 4px 8px;
  border-radius: 10px;
}

.konum {
  @apply flex text-lg items-center my-1;
  color: #3d3d43;
  font: bold;
}

.konum-bilgisi {
  margin-left: 0.25rem;
}

.btn {
  @apply flex w-full justify-center items-center p-1 rounded-lg my-3;
  background-color: #f67009;
  border: 2px solid transparent;
  color: #3d3d43;
}

.btn-icon {
  transition: 0.4s;
}

.btn:hover {
  color: #f67009;
  border: 2px solid #f67009;
  background-color: transparent;
}
